import React from 'react';
import AnswerItem from '../answer-item';
import './answer-list.css';

const AnswerList = ({ pokemons, onSelect }) => {
  if (!pokemons.length)
    return null;
  const items = pokemons.map((pokemon) => {
    return <AnswerItem onSelect={() => onSelect(pokemon.id)} key={pokemon.id} pokemon={pokemon} />;
  });
  return (
    <ul className="answer-list">
      {items}
    </ul>
  );
};

export default AnswerList;