const updatePokemonCollection = (state, action) => {
  if (state === undefined)
    return [];
  const { pokemonCollection } = state;
  switch (action.type) {

    case "ADD_POKEMON_TO_COLLECTION":
      const pokemon = action.payload;
      let hasPokemon = pokemonCollection.some(({ id }) => id === pokemon.id);
      return hasPokemon ? pokemonCollection : [...pokemonCollection, pokemon];

    case "REMOVE_POKEMON_FROM_COLLECTION":
      const id = action.payload;
      return pokemonCollection.filter((item) => item.id !== id);

    case "REMOVE_ALL_POKEMONS_FROM_COLLECTION":
      return [];

    default:
      return pokemonCollection;
  }
}

export default updatePokemonCollection;