const hash = (...args) => {
  return args.join();
}
const cacheDecorator = (func) => {
  let cache = new Map();
  return function (...x) {
    let key = hash(x);
    if (cache.has(key)) {
      return cache.get(key);
    }
    let result = func(...x);
    cache.set(key, result);
    return result;
  };
}

export default cacheDecorator;