import React from 'react';
import './search-control.css';

const SearchControl = ({term, setTerm}) => {
  return (
    <div className="input-wrapper">
      <input
        required
        value={term}
        onChange={(e) => setTerm(e.target.value)}
        className="search-input"
        type="text" />
      <i onClick={()=>setTerm("")} className="fas fa-times clear-input-icon"></i>
      <span className="custom-placeholder">search by name</span>
    </div>
  );
}

export default SearchControl;