import React, { useState } from 'react';
import { useSelector } from "react-redux";
import Spinner from '../spinner';
import TypesList from '../types-list';
import "./filter-control.css";

const FilterControl = ({ setFilterByType, filterByType }) => {
  const [showFilter, setShowFilter] = useState(false);
  const { types, loading, error } = useSelector(state => state.allTypesList);
  let typesList = null;
  if (error)
    typesList = <div>Error</div>;
  if (loading)
    typesList = <Spinner />;
  if (!loading && showFilter)
    typesList = (<TypesList
      types={types}
      setFilterByType={setFilterByType}
      filterByType={filterByType} />);

  const toggleFilter = () => {
    if (showFilter)
      setFilterByType(null);
    setShowFilter(!showFilter);
  }
  return (
    <div className="filter-control">
      <div className="filter-container">
        <span onClick={toggleFilter}
          className={`toggle-filter ${showFilter ? "active" : ""}`}>
          Types</span>
        {typesList}
      </div>
    </div>
  );
};

export default FilterControl;