const updatePokemonList = (state, action) => {
  if (state === undefined) {
    return {
      loading: true,
      error: false,
      pokemons: {}
    }
  }
  switch (action.type) {
    case "FETCH_POKEMONS_REQUEST":
      return {
        pokemons: {},
        loading: true,
        error: false,
      };
    case "FETCH_POKEMONS_SUCCESS":
      return {
        pokemons: action.payload,
        loading: false,
        error: false,
      };
    case "FETCH_POKEMONS_FAILURE":
      return {
        pokemons: {},
        loading: false,
        error: true,
      };
    default:
      return state.allPokemonsList;
  }
}

export default updatePokemonList;