import React, { useContext, useEffect, useState, useRef } from 'react';
import { pokemonContext } from '../pokemon-service-context';
import getRandomInt from '../../utilities/get-random-int';
import { successToast, errorToast } from "../../utilities";
import Spinner from '../spinner';
import './pokemon-game.css';
import QuestionCart from '../question-cart';
import AnswerList from '../answer-list';
import { useSelector } from "react-redux";

const PokemonGame = () => {
  const pokemonService = useContext(pokemonContext);
  const { pokemons: allPokemons, loading, error } = useSelector(state => state.allPokemonsList);
  const pokemonsInGameInitialState = {
    pokemons: [],
    question: null,
    gameLoading: false,
    gameError: false,
  };
  const [pokemonsInGame, setPokemonsInGame] = useState(pokemonsInGameInitialState);
  const { pokemons, question, gameLoading, gameError } = pokemonsInGame;
  const [score, setScore] = useState(0);
  const [maxScore, setMaxScore] = useState(0);
  const countOfAnswers = 5;
  const mounted = useRef(null);
  const [count, setCount] = useState(0);
  const [allPokemonsInGame, setAllPokemonsInGame] = useState([]);
  useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    }
  }, [])
  useEffect(() => {
    if (allPokemons.length)
      setCount(Math.floor(allPokemons.length / 1.5));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allPokemons]);

  useEffect(() => {
    if (count)
      setAllPokemonsInGame(allPokemons.slice(0, count));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [count]);

  useEffect(() => {
    if (allPokemonsInGame.length)
      startGame();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allPokemonsInGame]);

  const startGame = () => {
    setScore(0);
    if (score > maxScore)
      setMaxScore(score);
    nextRound();
  }
  const nextRound = () => {
    setPokemonsInGame({
      ...pokemonsInGameInitialState,
      gameLoading: true,
    });
    loadRandomPokemons(countOfAnswers)
      .then(({ pokemons, question }) => {
        if (mounted.current)
          setPokemonsInGame({
            pokemons,
            question,
            gameLoading: false,
            gameError: false,
          });
      })
      .catch(() => {
        if (mounted.current)
          setPokemonsInGame({
            ...pokemonsInGameInitialState,
            gameError: true,
            gameLoading: false,
          });
      });
  };

  const handleSelectPokemon = (id) => {
    if (id === question.id) {
      successToast(`Right answer!`);
      setScore(score + 1);
      nextRound();
    }
    else {
      errorToast(`Wrong answer! It was ${question.name}`);
      startGame();
    }
  }

  const loadRandomPokemons = async (count) => {
    const pokemonsIDs = [];
    while (pokemonsIDs.length < count) {
      const pokemonId = allPokemonsInGame[getRandomInt(allPokemonsInGame.length)].id;
      if (!pokemonsIDs.includes(pokemonId))
        pokemonsIDs.push(pokemonId);
    }
    const promises = pokemonsIDs.map(item => pokemonService.getPokemonDetails(item));
    const pokemons = await Promise.all(promises);
    const idx = getRandomInt(pokemons.length);
    const question = pokemons[idx];
    return {
      pokemons,
      question,
    };
  };


  if (error)
    return <div>Can't load all pokemons</div>;
  if (loading)
    return <Spinner />;


  const generateContent = () => {
    if (gameError)
      return <div>Can't load random pokemons</div>;
    if (gameLoading)
      return <Spinner />;
    return (
      <>
        <QuestionCart pokemon={question} />
        <AnswerList onSelect={handleSelectPokemon} pokemons={pokemons} />
      </>
    );
  };
  const content = generateContent();
  return (
    <div className="pokemon-game">
      <span className="game-score">Max Score: {maxScore}</span>
      <span className="game-score">Current Score: {score}</span>
      {content}
    </div>
  );
}

export default PokemonGame;