import React from 'react';
import './answer-item.css';
const AnswerItem = ({ pokemon, onSelect }) => {
  return (
    <li onClick={() => onSelect()} className="answer-item">
      <div className="answer-cart">
        <h2>{pokemon.name}</h2>
        <img src={pokemon.frontDefault} alt="img" />
      </div>
    </li>
  );
};

export default AnswerItem;