import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import "./header.css";
const Header = () => {
  const [opened, setOpened] = useState(false);
  const toggleOpened = () => {
    setOpened(opened => !opened);
  }
  let btnClassNames = "menu-btn";
  let navClassNames = "nav-list";

  if (opened) {
    btnClassNames += " opened";
    navClassNames += " opened";
  }

  return (
    <header>
      <Link to="/"><h1>PokeLib</h1></Link>
      <nav>
        <ul className={navClassNames}>
          <li onClick={toggleOpened} className="nav-item"><Link to="/library/">Library</Link></li>
          <li onClick={toggleOpened} className="nav-item"><Link to="/collection/">Collection</Link></li>
          <li onClick={toggleOpened} className="nav-item"><Link to="/game">Game</Link></li>
        </ul>
      </nav>
      <div onClick={toggleOpened} className={btnClassNames}>
        <div className="btn-burger"></div>
      </div>
    </header>
  )
}

export default Header;