import React, { useRef, useEffect } from 'react';
import './pokemon-stats.css';

const PokemonStats = ({ closeModal, name, stats, type }) => {
  const statsItems = stats.map(({ name, stat }, i) => {
    return (
      <li className="stats-item" key={i}>{name}: {stat}</li>
    );
  });
  const handleOuterClick = ({ target }) => {
    if (!modalRef.current.contains(target))
      closeModal();
  }
  const modalRef = useRef(null);

  useEffect(() => {
    document.addEventListener("click", handleOuterClick);
    return () => {
      document.removeEventListener("click", handleOuterClick);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div ref={modalRef} className="modal">
      <i onClick={closeModal} className="fas fa-times close-icon"></i>
      <h2 className={type}>{name}</h2>
      <ul className="stats-list">
        {statsItems}
      </ul>
    </div>
  );
}

export default PokemonStats;