import React, { useState } from 'react';
import AnimateHeight from 'react-animate-height';
import PokemonStats from '../pokemon-stats';
import "./pokemon-info.css";

const PokemonInfo = ({ pokemon, onClose }) => {
  const { name, weight, height, backDefault, frontDefault, types, stats } = pokemon;
  const pokemonNameClassNames = "name " + types[0];
  const pokemonContainerClassNames = `pokemon-info  border-${types[0]}`;

  const [opened, setOpened] = useState(false);
  const [showModal, setShowModal] = useState(false);

  let typesClassNames = "types";
  if (opened)
    typesClassNames += " active";

  const modal = showModal ? (
    <PokemonStats closeModal={() => setShowModal(false)} name={name} type={types[0]} stats={stats} />
  ) : null;

  const generateTypes = (types) => {
    if (types.length) {
      return types.map((item, idx) => {
        return (
          <li
            className="pokemon-info-item second-level"
            key={item}>{idx + 1}: <span className={`${item} type-icon icon-${item}`}>{item}</span>
          </li>
        );
      });
    }
    return null;
  }

  const toggleTypes = () => {
    setOpened(!opened);
  }

  const frontImage = frontDefault ? <img src={frontDefault} alt="frontImage" /> : null;
  const backImage = backDefault ? <img src={backDefault} alt="backImage" /> : null;

  return (
    <>
      <div className={pokemonContainerClassNames}>
        <i onClick={() => onClose()} className="fas fa-times close-icon"></i>
        <h2 className={pokemonNameClassNames}>{name}</h2>
        <ul className="pokemon-info-list">
          <li className="pokemon-info-item">Weight: {weight}</li>
          <li className="pokemon-info-item">Height: {height}</li>
          <li className="pokemon-info-item">
            <span className={typesClassNames} onClick={toggleTypes}>Types:</span>
            <AnimateHeight id='example-panel'
              duration={500}
              height={opened ? "auto" : 0}>
              <ul className="pokemon-types-list">
                {generateTypes(types)}
              </ul>
            </AnimateHeight>
          </li>

        </ul>
        <div className="sprites">
          {frontImage}
          {backImage}
        </div>
        <button onClick={() => setShowModal(true)} className="btn btn-stats">Show Stats</button>
      </div>
      {modal}
    </>
  );
};
export default PokemonInfo;