import React from 'react';
import './question-cart.css';

const QuestionCart = ({ pokemon }) => {

  if (!pokemon)
    return null;
  const types = pokemon.types.map((item) => {
    return <li key={item}>{item}</li>
  });
  console.log(pokemon);
  return (
    <div className={`question-cart border-${pokemon.types[0]}`}>
      <ul className="question-list">
        <li>Height: {pokemon.height}</li>
        <li>Width: {pokemon.weight}</li>
        <ul className="question-types">
          Types:
           {types}
        </ul>
      </ul>
    </div>
  );
}

export default QuestionCart;