const updateTypesList = (state, action) => {
  if (state === undefined) {
    return {
      loading: true,
      error: false,
      types: {}
    }
  }
  switch (action.type) {
    case "FETCH_TYPES_REQUEST":
      return {
        types: {},
        loading: true,
        error: false,
      };
    case "FETCH_TYPES_SUCCESS":
      return {
        types: action.payload,
        loading: false,
        error: false,
      };
    case "FETCH_TYPES_FAILURE":
      return {
        types: {},
        loading: false,
        error: true,
      };
    default:
      return state.allTypesList;
  }
}

export default updateTypesList;