import React from "react";
import PokemonDetails from "../pokemon-details/pokemon-details";
import PokemonList from "../pokemons-list";
import "./pokemon-library.css";

const PokemonLibrary = ({urlPage, urlPokemon}) => {
  return (
    <div className="pokemon-library">
      <div className="list-wrapper">
        <PokemonList urlPage={urlPage} urlPokemon={urlPokemon} />
      </div>

      <div className="details-wrapper">
        <PokemonDetails urlPokemon={urlPokemon} />
      </div>
    </div>
  );
};

export default PokemonLibrary;
