import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PokemonInfo from '../pokemon-info';
import { pokemonRemoved, allPokemonsRemoved } from "../../actions";
import "./pokemon-collection.css";
import { errorToast } from '../../utilities';
import SearchControl from '../search-control';

const PokemonCollectionWrapper = () => {
  const dispatch = useDispatch();
  const removePokemon = (id) => {
    dispatch(pokemonRemoved(id));
  }
  const removeAllPokemons = () => {
    dispatch(allPokemonsRemoved());
  }

  const pokemonCollection = useSelector(state => state.pokemonCollection);
  const [term, setTerm] = useState("");

  if (pokemonCollection.length) {
    const result = pokemonCollection.filter(({ name }) => {
      return name.toLowerCase().includes(term.toLowerCase());
    });

    const handleRemovePokemon = (pokemon) => {
      removePokemon(pokemon.id);
      errorToast(`${pokemon.name} was removed`);
    }

    const handleRemoveAllPokemons = () => {
      removeAllPokemons();
      errorToast(`All pokemons were removed`);
    }

    const content = result.length ? <PokemonCollection
      removePokemon={handleRemovePokemon}
      pokemonCollection={result} /> : (<div>no matching</div>);

    return (
      <div className="pokemon-collection-wrapper">
        <button className="btn" onClick={handleRemoveAllPokemons}>Delete All</button>
        <SearchControl setTerm={setTerm} term={term}/>
        {content}
      </div>
    );
  }
  return (
    <div>Collection is empty</div>
  );
};

const PokemonCollection = ({ removePokemon, pokemonCollection }) => {

  const generateItems = (items) => {
    return items.map((pokemon) => {
      return <PokemonInfo
        onClose={() => removePokemon(pokemon)}
        key={pokemon.id}
        pokemon={pokemon} />
    });
  };
  return (
    <ul className="pokemon-collection">{generateItems(pokemonCollection)}</ul>
  );
};

export default PokemonCollectionWrapper;