import updatePokemonList from "./update-pokemon-list";
import updatePokemonDetails from "./update-pokemon-details";
import updatePokemonCollection from "./update-pokemon-collection";
import updateTypesList from './update-types-list';
import updateFiltereByTypeList from "./update-filtered-by-type-list";

const reducer = (state, action) => {
  return {
    allPokemonsList: updatePokemonList(state, action),
    pokemonDetails: updatePokemonDetails(state, action),
    pokemonCollection: updatePokemonCollection(state, action),
    allTypesList: updateTypesList(state, action),
    filteredByTypeList: updateFiltereByTypeList(state, action),
  };
};

export default reducer;
