import React, { useContext, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchPokemon, pokemonDeselected, fetchFilteredByTypesPokemons } from "../../actions";
import PokemonItem from '../pokemon-item';
import "./pokemon-list.css";
import ReactPaginate from "react-paginate";
import Spinner from "../spinner";
import { withRouter } from 'react-router-dom';
import { pokemonContext } from '../pokemon-service-context';
import SearchControl from "../search-control";
import FilterControl from "../filter-control";

const PokemonListWrapper = props => {
  const {
    urlPage,
    urlPokemon,
    history,
  } = props;
  const pokemonService = useContext(pokemonContext);
  const dispatch = useDispatch();
  const { pokemons: allPokemons, loading: allPokemonsLoading, error: allPokemonsError } = useSelector(state => state.allPokemonsList);
  const { filteredList, loading: filteredLoading, error: filteredError } = useSelector(state => state.filteredByTypeList);
  let error;
  let loading;
  let pokemons;
  const onPage = 10;
  const initialPage = urlPage > 0 ? urlPage : 1;
  const [page, setPage] = useState(initialPage - 1);
  const [pageRange, setPageRange] = useState(3);
  const [term, setTerm] = useState("");
  const [filterByType, setFilterByType] = useState(null);
  const [pageCount, setPageCount] = useState(0);
  const loadPokemon = (id) => {
    dispatch(fetchPokemon(pokemonService)(id));
  };
  const loadFilteredByTypesPokemons = (id) => {
    dispatch(fetchFilteredByTypesPokemons(pokemonService)(id));
  }

  const selectPokemon = (id) => {
    history.push(id);
  }
  useEffect(() => {
    if (filterByType)
      loadFilteredByTypesPokemons(filterByType);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filterByType]);

  useEffect(() => {
    if (window.innerWidth < 916)
      setPageRange(1);
    else
      setPageRange(3);
  }, []);

  useEffect(() => {
    if (urlPokemon)
      loadPokemon(urlPokemon);
    else
      dispatch(pokemonDeselected());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [urlPokemon]);

  useEffect(() => {
    if (!urlPage) {
      setPage(0);
      history.push("/library/1/");
    }
  }, [history, urlPage]);

  const selectPage = ({ selected }) => {
    setPage(selected);
    history.push(`/library/${selected + 1}/${urlPokemon || ""}`);
  };
  if (filterByType) {
    loading = filteredLoading;
    error = filteredError;
    pokemons = filteredList;
  }
  else {
    loading = allPokemonsLoading;
    error = allPokemonsError;
    pokemons = allPokemons;
  }
  const onSearchChange = (value) => {
    setTerm(value);
    selectPage({ selected: 0 });
  }
  const onFilterByType = (value) => {
    setFilterByType(value);
    selectPage({ selected: 0 });
  }
  return (
    <React.Fragment>
      <SearchControl setTerm={onSearchChange} term={term} />
      <FilterControl setFilterByType={onFilterByType} filterByType={filterByType} />
      <PokemonListLogic error={error}
        loading={loading}
        pokemons={pokemons}
        term={term}
        onPage={onPage}
        selectPokemon={selectPokemon}
        urlPokemon={urlPokemon}
        page={page}
        setPageCount={setPageCount} />
      <ReactPaginate
        previousLabel='<'
        nextLabel='>'
        breakLabel='...'
        breakClassName='break-me'
        pageCount={pageCount}
        marginPagesDisplayed={1}
        pageRangeDisplayed={pageRange}
        onPageChange={selectPage}
        containerClassName='pagination'
        activeClassName='active'
        initialPage={page}
        forcePage={page} />
    </React.Fragment>
  );
};

const PokemonListLogic = ({ loading,
  error,
  term,
  pokemons,
  onPage,
  selectPokemon,
  urlPokemon,
  page,
  setPageCount }) => {
  let filteredPokemons;
  let pageCount = 0;
  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    pageCount = Math.ceil(filteredPokemons?.length / onPage);
    setPageCount(pageCount);
  });
  if (error)
    return <div>Error</div>;
  if (loading)
    return <Spinner />;
  if (!pokemons.length)
    return <div>List is empty</div>;
  filteredPokemons = pokemons.filter(({ name }) => {
    return name.toLowerCase().includes(term.toLowerCase());
  });
  if (!filteredPokemons.length)
    return <div>no matching</div>;
  const showPokemons = filteredPokemons.slice(page * onPage, (1 + page) * onPage);

  if (!showPokemons.length)
    return <div>page is not exist</div>;
  return (
    <PokemonList pokemons={showPokemons}
      selectPokemon={selectPokemon}
      urlPokemon={urlPokemon}
      setPageCount={setPageCount}
      pageCount={pageCount} />
  );
}

const PokemonList = ({
  pokemons,
  selectPokemon,
  urlPokemon }) => {

  return (
    <ul className="pokemon-list">
      {pokemons.map(item => {
        const isActive = urlPokemon === item.id;
        return <PokemonItem isActive={isActive} loadPokemon={() => selectPokemon(item.id)} key={item.id} item={item} />;
      })}
    </ul>
  );
};

export default withRouter(PokemonListWrapper);
