import React from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { withRouter } from "react-router-dom";
import { pokemonAdded, pokemonDeselected, pokemonRemoved } from '../../actions';
import PokemonInfo from "../pokemon-info";
import Spinner from "../spinner";
import { successToast, errorToast } from "../../utilities";
import "./pokemon-details.css";


const PokemonDetails = ({ buttonClassNames, buttonText, pokemon, deselectPokemon, detailsButtonHandler }) => {
  return (
    <div className="pokemon-details">
      <PokemonInfo onClose={deselectPokemon} pokemon={pokemon} />
      <button onClick={detailsButtonHandler} className={buttonClassNames}>{buttonText}</button>
    </div>
  );
};

const PokemonDetailsWrapper = ({ history, match }) => {

  const { loading, error, pokemon } = useSelector(state => state.pokemonDetails);
  const dispatch = useDispatch();

  const deselectPokemon = () => {
    dispatch(pokemonDeselected());
    history.push(`/library/${match.params.page}/`);
  }

  const inCollection = useSelector(
    state =>
      state.pokemonCollection.some(
        item =>
          item.id === pokemon?.id, shallowEqual
      )
  );

  const detailsButtonHandler = () => {
    return inCollection ?
      removePokemon() :
      addPokemon();
  }
  const removePokemon = () => {
    dispatch(pokemonRemoved(pokemon.id));
    errorToast(`${pokemon.name} was removed`);
  }
  const addPokemon = () => {
    dispatch(pokemonAdded(pokemon));
    successToast(`${pokemon.name} was added`);
  }
  const buttonText = inCollection ? "Remove From Collection" : "Add To Collection";
  let buttonClassNames = "btn";
  if (inCollection)
    buttonClassNames += " remove-btn";

  if (error) {
    return <div>Error</div>;
  }
  if (loading) {
    return <Spinner />
  }
  if (!pokemon) {
    return <div>Select a Pokemon</div>;
  }
  return (
    <PokemonDetails
      inCollection={inCollection}
      buttonText={buttonText}
      deselectPokemon={deselectPokemon}
      detailsButtonHandler={detailsButtonHandler}
      pokemon={pokemon}
      buttonClassNames={buttonClassNames} />
  );
};

export default withRouter(PokemonDetailsWrapper);