const updateFiltereByTypeList = (state, action) => {
  if (state === undefined) {
    return {
      loading: true,
      error: false,
      filteredList: {}
    }
  }
  switch (action.type) {
    case "FETCH_FILTERED_BY_TYPE_REQUEST":
      return {
        filteredList: {},
        loading: true,
        error: false,
      };
    case "FETCH_FILTERED_BY_TYPE_SUCCESS":
      return {
        filteredList: action.payload,
        loading: false,
        error: false,
      };
    case "FETCH_FILTERED_BY_TYPE_FAILURE":
      return {
        filteredList: {},
        loading: false,
        error: true,
      };
    default:
      return state.filteredByTypeList;
  }
}

export default updateFiltereByTypeList;