const pokemonsRequested = () => {
  return {
    type: "FETCH_POKEMONS_REQUEST",
  };
};
const pokemonsLoaded = (newPokemons) => {
  return {
    type: "FETCH_POKEMONS_SUCCESS",
    payload: newPokemons,
  };
};
const pokemonsError = (error) => {
  return {
    type: "FETCH_POKEMONS_FAILURE",
    payload: error,
  };
};
const typesRequested = () => {
  return {
    type: "FETCH_TYPES_REQUEST",
  };
}
const typesLoaded = (types) => {
  return {
    type: "FETCH_TYPES_SUCCESS",
    payload: types,
  };
};
const typesError = (error) => {
  return {
    type: "FETCH_TYPES_FAILURE",
    payload: error,
  };
};

const filteredByTypeRequested = () => {
  return {
    type: "FETCH_FILTERED_BY_TYPE_REQUEST",
  };
}
const filteredByTypeLoaded = (types) => {
  return {
    type: "FETCH_FILTERED_BY_TYPE_SUCCESS",
    payload: types,
  };
};
const filteredByTypeError = (error) => {
  return {
    type: "FETCH_FILTERED_BY_TYPE_FAILURE",
    payload: error,
  };
};

const pokemonRequested = () => {
  return {
    type: "FETCH_POKEMON_REQUEST",
  };
};
const pokemonLoaded = (newPokemon) => {
  return {
    type: "FETCH_POKEMON_SUCCESS",
    payload: newPokemon,
  };
};
const pokemonError = (error) => {
  return {
    type: "FETCH_POKEMON_FAILURE",
    payload: error,
  };
};

const pokemonAdded = (pokemon) => {
  return {
    type: "ADD_POKEMON_TO_COLLECTION",
    payload: pokemon,
  };
};
const pokemonDeselected = () => {
  return {
    type: "DESELECT_POKEMON",
  }
}
const pokemonRemoved = (id) => {
  return {
    type: "REMOVE_POKEMON_FROM_COLLECTION",
    payload: id,
  }
}
const allPokemonsRemoved = () => {
  return {
    type: "REMOVE_ALL_POKEMONS_FROM_COLLECTION",
  }
}


const fetchPokemons = (pokemonService) => (dispatch) => {
  dispatch(pokemonsRequested());
  pokemonService.getAllPokemons()
    .then((data) => dispatch(pokemonsLoaded(data)))
    .catch((error) => dispatch(pokemonsError(error)));
}

const fetchFilteredByTypesPokemons = (pokemonService) => (id) => (dispatch) => {
  dispatch(filteredByTypeRequested());
  pokemonService.getFilteredByTypePokemons(id)
    .then((data) => dispatch(filteredByTypeLoaded(data)))
    .catch((error) => dispatch(filteredByTypeError(error)));
}

const fetchTypes = (pokemonService) => (dispatch) => {
  dispatch(typesRequested());
  pokemonService.getAllTypes()
    .then((data) => dispatch(typesLoaded(data)))
    .catch((error) => dispatch(typesError(error)));
}

const fetchPokemon = (pokemonService) => (id) => (dispatch) => {
  dispatch(pokemonRequested());
  pokemonService.getPokemonDetails(id)
    .then((data) => dispatch(pokemonLoaded(data)))
    .catch((error) => dispatch(pokemonError(error)));
}

export {
  pokemonsError,
  pokemonsRequested,
  pokemonsLoaded,
  fetchPokemons,
  fetchPokemon,
  pokemonAdded,
  pokemonDeselected,
  pokemonRemoved,
  allPokemonsRemoved,
  fetchTypes,
  filteredByTypeRequested,
  filteredByTypeError,
  filteredByTypeLoaded,
  fetchFilteredByTypesPokemons,
}