import React, { useEffect, useContext } from "react";
import "./app.css";
import { PokemonLibrary, PokemonCollection } from "../pages/";
import Header from "../header";
import { Route, Switch } from 'react-router-dom';
import PokemonGame from "../pages/pokemon-game";
import { pokemonContext } from '../pokemon-service-context';
import { useDispatch } from "react-redux";
import { fetchPokemons, fetchTypes } from "../../actions"
function App() {

  const pokemonService = useContext(pokemonContext);
  const dispatch = useDispatch();
  const loadPokemons = () => {
    dispatch(fetchPokemons(pokemonService));
    
  };
  useEffect(() => {
    loadPokemons();
    dispatch(fetchTypes(pokemonService));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <main className="container">
      <Header />
      <Switch>
        <Route path="/" render={() => <div>Welcome to PokeLib</div>} exact />
        <Route path="/library/" exact component={PokemonLibrary} />
        <Route path="/library/:page/" exact render={({ match }) => {
          const { page } = match.params;
          return <PokemonLibrary urlPage={page} />
        }} />
        <Route path="/library/:page/:pokemon" render={({ match }) => {
          const { page, pokemon } = match.params;
          return <PokemonLibrary urlPage={page} urlPokemon={pokemon} />
        }} />
        <Route path="/collection" component={PokemonCollection} />
        <Route path="/game" component={PokemonGame} />
      </Switch>
    </main>
  );
}

export default App;
