import cacheDecorator from '../utilities/cache-decorator';

export default class PokemonService {
  _apiBase = 'https://pokeapi.co/api/v2/';

  _extractId = (item) => {
    const idRegExp = /\/([0-9]*)\/$/;
    return item.url.match(idRegExp)[1];
  }

  _transformListItem = (item) => {
    return {
      id: this._extractId(item),
      name: this._upFirstLetter(item.name),
    }
  }
  _transfromPokemonStats = (stats) => {
    return stats.map(({ base_stat, stat: { name } }) => {
      return {
        name: this._upFirstLetter(name),
        stat: base_stat,
      }
    });
  }
  _upFirstLetter = (name) => {
    return name[0].toUpperCase() + name.slice(1);
  }
  _transformPokemonDetails = (item) => {
    return {
      name: this._upFirstLetter(item.name),
      weight: item.weight,
      height: item.height,
      backDefault: item.sprites.back_default,
      frontDefault: item.sprites.front_default,
      types: this._transformTypes(item.types),
      id: item.id,
      stats: this._transfromPokemonStats(item.stats),
    }
  }
  _transformTypes = (types) => {
    return types.map(({ type: { name } }) => name);
  }

  getPokemonDetails = async (id) => {
    let response = await fetch(`${this._apiBase}pokemon/${id}/`);
    let res = await response.json();
    return this._transformPokemonDetails(res);
  }
  getPokemonDetails = cacheDecorator(this.getPokemonDetails);

  getAllPokemons = async () => {
    let response = await fetch(`${this._apiBase}pokemon`);
    let res = await response.json();
    const count = res.count;
    response = await fetch(`${this._apiBase}pokemon?limit=${count}`);
    res = await response.json();
    return res.results.map(this._transformListItem);
  }
  getAllPokemons = cacheDecorator(this.getAllPokemons);

  getAllTypes = async () => {
    let response = await fetch(`${this._apiBase}type`);
    let res = await response.json();
    return res.results.map(this._transformListItem);
  }
  getAllTypes = cacheDecorator(this.getAllTypes);

  getFilteredByTypePokemons = async (id) => {
    let response = await fetch(`${this._apiBase}type/${id}`);
    let res = await response.json();
    return res.pokemon.map(({ pokemon }) => {
      return this._transformListItem(pokemon);
    });
  };
  getFilteredByTypePokemons = cacheDecorator(this.getFilteredByTypePokemons);
}
