import React from 'react';
import './types-list.css';

const TypesList = ({ types, setFilterByType, filterByType }) => {
  return (
    <ul className="types-container">
      {types.map(({ id, name }) => {
        return (<li
          className={`type-selector ${name.toLowerCase()} type-icon icon-${name.toLowerCase()} ${filterByType === id ? "active" : ""}`}
          key={id}
          onClick={() => setFilterByType(id)}>{name}</li>);
      })}
    </ul>
  );
}

export default TypesList;