const updatePokemonDetails = (state, action) => {
  if (state === undefined) {
    return {
      loading: false,
      error: false,
      pokemon: null
    }
  }
  switch (action.type) {
    case "FETCH_POKEMON_REQUEST":
      return {
        pokemon: null,
        loading: true,
        error: false,
      };
    case "FETCH_POKEMON_SUCCESS":
      return {
        pokemon: action.payload,
        loading: false,
        error: false,

      };
    case "FETCH_POKEMON_FAILURE":
      return {
        pokemon: null,
        loading: false,
        error: true,
      }
    case "DESELECT_POKEMON":
      return {
        loading: false,
        error: false,
        pokemon: null
      }
    default:
      return state.pokemonDetails;
  }
}

export default updatePokemonDetails;